.header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-bottom: 40px;

}

.header__logo {
    display: flex;
    align-items: center;
    
    text-decoration: none;
}

.header__img {
    max-width: 100%;
    margin-right: 15px;
}

.header__title h1 {
    font-weight: 800;
    font-size: 24px;
    line-height: 29px;

    color: #181818;
}

.header__title h3 {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;

    color: #7b7b7b;
}

.header__cart {
    text-decoration: none;
}


@media(max-width: 500px) {
    .header__title h1 {
        font-size: 20px;
        line-height: 20px;
    }
    
    .header__title h3 {
        display: none;
    }
}

@media(max-width: 375px) {
    .header {
        margin-bottom: 20px;
    }
}
